  /* Navbar */

  .nav__links {
    display: none;
    position: absolute;
    top: 100px;
    right: -100%;
    width: 100%;
    text-align: center;
    transition: 0.3s;
  }

  .nav__links li {
    top: 100px;
    display: block;
    padding: 10px 20px;
    background-color: rgba(36, 37, 42, 0.452) !important;
  }

  .nav__links a {
    background: transparent;
  }

  .nav__links button {
    margin-right: 18px;
  }

  .nav__links.open {
    display: block;
    right: 0;
    background-color: rgba(36, 37, 42, 0.452);
    z-index: 1;
  }

  /* Hamburger Menu */

  .menu-btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    transition: all .5s ease-in-out;
    background-color: transparent;
  }

  .menu-btn__burger {
    width: 25px;
    height: 4px;
    border-radius: 5px;
    background: #fff;
    transition: all .5s ease-in-out;
  }
  
  .menu-btn__burger::before,.menu-btn__burger::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 4px;
    border-radius: 5px;
    background: #fff;
    transition: all .5s ease-in-out;
  }

  .menu-btn__burger::before {
    transform: translateY(-16px);
  }

  .menu-btn__burger::after {
    transform: translateY(16px);
  }


  /* ANIMATION */

  .menu-btn.open .menu-btn__burger {
    transform: translateY(-10px);
    background: transparent;
  }

  .menu-btn.open .menu-btn__burger::before {
    transform: rotate(45deg) translate(0px, -0px);
  }

  .menu-btn.open .menu-btn__burger::after {
    transform: rotate(-45deg) translate(0px, 0px);
  }

}
